@import url(https://fonts.googleapis.com/css?family=Maven+Pro&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons&display=swap);
.header{display:flex;justify-content:space-between;color:rgba(255,255,255,.774);background-color:#000;padding:0 20px}.header h1{padding:10px;margin:0}.helpAndBackButton,.backToStub4Button,.helpButton{display:inline-flex;align-items:center;justify-content:center;background-color:#3d98ff;border:none;color:#000;font-size:14px;font-weight:bold;border-radius:2px;min-width:140px;margin:10px 0}
.indexListItem{padding-bottom:5px}.linkWithoutColor,.subDocsLink,.selectedSubDocsLink,.docsLink,.selectedDocsLink{color:inherit}.docsLink,.selectedDocsLink{font-size:20px}.selectedDocsLink{font-weight:bold;padding-left:15px}.subDocsLink,.selectedSubDocsLink{font-size:16px;padding-left:25px}.selectedSubDocsLink{font-weight:bold;padding-left:30px}
.contentsTitle{color:#3d98ff}.code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace;background-color:#000}.highlightedTerm{color:#3d98ff;font-weight:bold}.linkToOtherDocs{color:#3d98ff}.externalLink{color:#3d98ff}.screenshot{width:90%}
.Docs{height:100%;display:flex;flex-direction:column;overflow-y:scroll}.docsContent{color:rgba(255,255,255,.774);display:flex;flex:2 1}.column{flex:1 1;padding-left:20px;background-color:#3a3939}.centerColumn{flex:2 1;padding:0 20px;background-color:#1e1e1e;border-left:1px solid #fff}
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit;font-family:"Maven Pro",sans-serif}body{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body,#root{height:100%;margin:0}
